import { IRequestData, ISettingsIAM } from "@stechquick/algae/lib/quick/IPlateauUI";
import { CreatePromiseData } from "../../common/shrimp/interfaces/IPromiseData";
import { IDomElement } from "../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { doPostMessage, IAmPopupMessage, IAmToken, IGetIAmToken } from "./src/interfaces/externalInterfaces";
import { init as websdkInit, createRenderer } from "./websdk";

declare var websdk: {
    init: typeof websdkInit,
    createRenderer: typeof createRenderer,
}

async function init() {
    window.addEventListener("message", message => {
        const data = message.data as IAmPopupMessage;
        if (data.type != "iamOptions") { return; }
        if (!data.options) { throw new Error("no options received"); }
        if (data.tokens) { setTokens(data.tokens, data.sessionStorageNamePrefix ?? "") }
        window["baseWindow"] = data.parentWindow;
        websdk.init({ ...data.options, iamInPopup: true }).then(async (websdkContainer) => {
            var plateauUIRenderer = await websdk.createRenderer({ mountPoint: document.getElementById("plateau_ui_mountPoint") as IDomElement});
            plateauUIRenderer.open("static/qjsons/samplesFirstPage.qjson");
        });
    });

    const setTokens = (tokenInfo: IGetIAmToken, sessionStorageNamePrefix: string) => {
        if (!tokenInfo || !tokenInfo.idToken || !tokenInfo.refreshToken || !tokenInfo.token) return undefined;
        sessionStorage.setItem(sessionStorageNamePrefix + "kcIdToken", tokenInfo.idToken);
        sessionStorage.setItem(sessionStorageNamePrefix + "kcRefreshToken", tokenInfo.refreshToken);
        sessionStorage.setItem(sessionStorageNamePrefix + "kcToken", tokenInfo.token);
    }
    doPostMessage(opener, {type: "iamReady"});
}

init();
export {};